import { reqDetailList,reqAddOrUpdateCart } from "@/api";


const state = {
    detailList:{}
}

const mutations = {
    GETDETAILLIST(state,detailList){
        state.detailList = detailList
    }
}

const actions = {
    async getDetailList({commit},skuId){
        let result = await reqDetailList(skuId)
        console.log(result)
        if(result.code == 200){
            console.log(result)
            commit('GETDETAILLIST',result.data)
        }
    },
    async addOrUpdateCart({ state, commit, dispatch }, { skuId, skuNum }){
        let result = await reqAddOrUpdateCart( skuId, skuNum )
        console.log(result)
        if(result.code == 200){
            return 'ok'
        }else{
            return Promise.reject()
        }
    }
    
}

const getters = {
    //
    categoryView() {
        //:
        //:state.detailInfo,.categoryView->undefined
        //state.detailInfo,,{7K}
        //:undefined
        return state.detailList.categoryView || {}
   },
   //
   skuInfo() {
        return state.detailList.skuInfo || {}
   },
   //
   spuSaleAttrList() {
        return state.detailList.spuSaleAttrList || []
   }
}

export default{
    namespaced:true,
    state,
    mutations,
    actions,
    getters
}