import { register, reqRegisterCode, login, reqUserInfo, loginOut } from '@/api'


const state = {
    code: '',
    token: localStorage.getItem("TOKEN"),
    nickName: ''
}

const mutations = {
    GETREGISTERCODE(state, code) {
        state.code = code
    },
    SET_TOKEN(state, token) {
        state.token = token
    },
    GETUSERINFO(state, nickName) {
        state.nickName = nickName
    },
    CLEAR(state){
        //
        state.token = '';
        state.nickName = '';
        //
        localStorage.removeItem('TOKEN');
    }
}

const actions = {
    async getRegisterCode({ commit }, phone) {
        const result = await reqRegisterCode(phone)
        if (result.code == 200) {
            commit('GETREGISTERCODE', result.data)
            return 'ok'
        } else {
            new Promise.reject()
        }
    },
    async userRegister({ commit }, data) {
        const result = await register(data)
        if (result.code == 200) {
            //
            return 'ok';
        } else {
            //
            return Promise.reject(new Error(result.message));
        }
    },
    async userLogin({ commit }, data) {
        let result = await login(data)
        //
        if (result.code == 200) {
            commit('SET_TOKEN', result.data.token);
            //:token
            localStorage.setItem('TOKEN', result.data.token);
            return 'ok';
        } else {
            //
            return Promise.reject(new Error(result.message));
        }
    },
    async getUserInfo({ commit }) {
        const result = await reqUserInfo()
        if (result.code == 200) {
            commit('GETUSERINFO', result.data.nickName)
        } else {
            new Promise.reject()
        }
    },
    async loginOut({ commit }) {
        const result = await loginOut()
        if (result.code == 200) {
            commit('CLEAR');
            return 'ok';
        }else {
          new Promise.reject()
        } 
    }
}

const getters = {

}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}