<template>
  <div class="order-right">
    <div class="order-content">
      <div class="title">
        <h3></h3>
      </div>
      <div class="chosetype">
        <table>
          <thead>
            <tr>
              <th width="29%"></th>
              <th width="31%"></th>
              <th width="13%"></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
        </table>
      </div>
      <!---->
      <div class="orders">
        <!---->
        <table class="order-item" v-for="(order,index) in  list" :key="order.id">
          <thead>
            <tr>
              <th colspan="5">
                <span class="ordertitle"
                  >{{order.createTime}} {{order.outTradeNo}}
                  <span class="pull-right delete"
                    ><img src="../images/delete.png" /></span
                ></span>
              </th>
            </tr>
          </thead>
          <tbody>
             <!--  -->
            <tr v-for="(cart,index) in order.orderDetailList" :key="index">
              <td width="60%">
                <div class="typographic">
                  <img :src="cart.imgUrl" style="width:100px;height:100px"/>
                  <a class="block-text">{{cart.skuName}}</a>
                  <span>x{{cart.skuNum}}</span>
                  <a href="#" class="service"></a>
                </div>
              </td>
              <td :rowspan="order.orderDetailList.length" v-show="index==0" width="8%" class="center">{{order.consignee}}</td>
              <td :rowspan="order.orderDetailList.length" v-show="index==0" width="13%" class="center">
                <ul class="unstyled">
                  <li>{{order.totalAmount}}.00</li>
                  <li></li>
                </ul>
              </td>
              <td :rowspan="order.orderDetailList.length" v-show="index==0" width="8%" class="center">
                <a href="#" class="btn">{{order.orderStatusName}}</a>
              </td>
              <td :rowspan="order.orderDetailList.length"  v-show="index==0" width="13%" class="center">
                <ul class="unstyled">
                  <li>
                    <a href="mycomment.html" target="_blank">|</a>
                  </li>
                </ul>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="choose-order">
             <!-- 
                total:
                pageSize:
                pageNo:
                pagerCount:
                currentPage:

              -->
              <el-pagination
                @current-change="currentPage"
                :page-size="limit"
                layout="total,  prev, next, jumper"
                :total="total" background>
                :pager-count="9">
              </el-pagination>
      </div>
    </div>
    <!---->
    <div class="like">
      <h4 class="kt"></h4>
      <ul class="like-list">
        <li class="likeItem">
          <div class="p-img">
            <img src="../images/itemlike01.png" />
          </div>
          <div class="attr">
            <em>DELLIns 15MR-7528SS 15  </em>
          </div>
          <div class="price">
            <em></em>
            <i>3699.00</i>
          </div>
          <div class="commit">6</div>
        </li>
        <li class="likeItem">
          <div class="p-img">
            <img src="../images/itemlike02.png" />
          </div>
          <div class="attr">AppleiPhone 6s/6s Plus 16G 64G 128G</div>
          <div class="price">
            <em></em>
            <i>4388.00</i>
          </div>
          <div class="commit">700</div>
        </li>
        <li class="likeItem">
          <div class="p-img">
            <img src="../images/itemlike03.png" />
          </div>
          <div class="attr">DELLIns 15MR-7528SS 15  </div>
          <div class="price">
            <em></em>
            <i>4088.00</i>
          </div>
          <div class="commit">700</div>
        </li>
        <li class="likeItem">
          <div class="p-img">
            <img src="../images/itemlike04.png" />
          </div>
          <div class="attr">DELLIns 15MR-7528SS 15  </div>
          <div class="price">
            <em></em>
            <i>4088.00</i>
          </div>
          <div class="commit">700</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import {reqMyOrderList} from '@/api'
export default {
  name: "",
  data() {
    return {
      //
      page: 1,
      //
      limit: 3,
      //
      list:[],
      //
      total:0
    };
  },
  mounted() {
    //
    this.getData();
  },
  methods: {
    //
    async getData() {
      //:page|limit
      const { page, limit } = this;
      let result = await reqMyOrderList(page, limit);
      if (result.code == 200) {
          this.list = result.data.records;
          this.total = result.data.total;
      }
    },
    //
    currentPage(page){
        //
        this.page = page;
        this.getData();
    }
  },
};
</script>

